







































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import dayjs from 'dayjs';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import clone from '@/lib/clone';

@Component({
  components: {Tabs}
})
export default class Home extends Vue {

  beforeCreate() {
    this.$store.commit('fetchChooseDate',);
    this.$store.commit('fetchRecords');
  }

  get recordList() {
    // eslint-disable-next-line no-undef
    return (this.$store.state as RootState).recordList;
  }

  chooseDate(e: any) {
    let value = e.target.value;
    this.$store.commit('setChooseDate', value);
  }

  get sumNumber() {
    const dayList = this.dayList;
    return dayList.reduce((pre, now) => {
      return now.amount + pre;
    }, 0);
  }

  get dayList() {
    const nowDate = this.$store.state.chooseDate;
    const recordList = this.recordList.filter(r => r.type === this.type);
    const newList = clone(recordList).filter(r => dayjs(r.createAt).format('YYYY-MM-DD') === this.value);
    return newList;
  }

  get value() {
    return this.$store.state.chooseDate;
  }


  type = '-';
  recordTypeList = recordTypeList;

}
