






























import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import intervalList from '@/constants/intervalList';
import recordTypeList from '@/constants/recordTypeList';
import Tags from '@/components/Money/Tags.vue';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Chart from '@/components/Chart.vue';
import _, {values} from 'lodash';

const oneDay = 86400 * 1000;
@Component(
    {
      components: {Chart, Tabs,}
    }
)
export default class Statistics extends Vue {

  get chartOptions() {
    const today = new Date();
    const array = [];

    for (let i = 0; i <= 29; i++) {
      const date = dayjs(today).subtract(i, 'day').format('YYYY-MM-DD');
      let _groupedList = (this.groupedList.filter(r => r.title === date));
      let amountValue: number | undefined = 0;
      if (_groupedList.length === 0) {
        amountValue = 0;
      } else {
        amountValue = _groupedList[0].total;
      }
      array.push({date: date, value: amountValue});
      array.sort((a, b) => {
        if (a.date > b.date) {
          return 1;
        } else if (a.date < b.date) {
          return -1;
        } else {
          return 0;
        }
      });
    }

    const keys = array.map(item => item.date);
    const values = array.map(item => item.value);
    return {
      grid: {
        bottom: 40,
        right: 0,
        left: 0,
        top: 40,
      },
      xAxis: {
        type: 'category',
        data: keys,
        axisTick: {alignWithLabel: true},
        axisLine: {lineStyle: {color: '#666'}},
        axisLabel: {
          formatter: function (value: string) {
            return value.substr(5);
          }
        }
      },
      yAxis: {
        type: 'value',
        show: false
      },
      series: [
        {
          symbol: 'circle',
          symbolSize: 10,
          itemStyle: {borderWidth: 1, radius: 10},
          data: values,
          type: 'line'
        }
      ],
      tooltip: {
        show: true, triggerOn: 'click ',
        formatter: '{c}',
        position: 'top'
      }
    };
  }


  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, 'day')) {
      return '';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '';
    } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
      return '';
    } else if (day.isSame(now, 'year')) {
      return day.format('MMDD');
    } else {
      return day.format('YYYYMMDD');
    }
  }

  mounted() {
    (this.$refs.chartWrapper as HTMLDivElement).scrollLeft = 9999;
  }

  // eslint-disable-next-line no-undef
  tagString(tags: Tag[]) {
    return tags.length === 0 ? '' : tags.map(t => t.name).join('');
  }


  get recordList() {
    // eslint-disable-next-line no-undef
    return (this.$store.state as RootState).recordList;
  }


  get groupedList() {
    const {recordList} = this;


    const newList = clone(recordList).filter(r => r.type === this.type).sort((b, a) => dayjs(a.createAt).valueOf() - dayjs(b.createAt).valueOf());
    if (newList.length === 0) {
      return [];
    }
    type Result = { title: string, total?: number, items: RecordItem[] }[]
    const result: Result = [{title: dayjs(newList[0].createAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createAt), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => {
        return sum + item.amount;
      }, 0);

    });

    return result;
  }


  beforeCreate() {
    this.$store.commit('fetchRecords');
  }


  type = '-';
  interval = 'day';
  intervalList = intervalList;
  recordTypeList = recordTypeList;
}
